@font-face {
  font-family: Myriad Pro;
  src: url(../fonts/Myriad_Pro_Regular.ttf); }

.header {
  height: 793px; }
  .header * {
    color: #ffffff; }
  .header .nav, .header .header-title {
    z-index: 1; }
  .header .overlay {
    display: block;
    width: 100%;
    height: 100%;
    background-color: #392d3bbf;
    position: absolute;
    z-index: -1; }
  .header img {
    min-height: 100%;
    min-width: 100%;
    width: auto;
    height: auto;
    position: absolute;
    z-index: -1; }
  .header .header-title {
    text-align: center;
    width: 290px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -106%); }
    .header .header-title span {
      display: block; }
      .header .header-title span:nth-child(1) {
        font-size: 9px;
        font-weight: 100;
        font-family: 'Myriad Pro', Arial, Helvetica, sans-serif;
        letter-spacing: 3px;
        margin-bottom: 7px; }
      .header .header-title span:nth-child(2) {
        font-size: 57px;
        font-weight: 900;
        letter-spacing: 3px;
        padding: 6px 0;
        border-top: 5px solid #dc5352;
        border-left: 5px solid #dc5352;
        border-right: 5px solid #dc5352;
        position: relative; }
        .header .header-title span:nth-child(2)::before {
          content: '';
          border-bottom: 6px solid #dc5352;
          display: block;
          width: 75px;
          position: absolute;
          bottom: 0;
          right: 0; }
        .header .header-title span:nth-child(2)::after {
          content: '';
          border-bottom: 6px solid #dc5352;
          display: block;
          width: 75px;
          position: absolute;
          bottom: 0;
          left: 0; }
      .header .header-title span:nth-child(3) {
        font-size: 17px;
        letter-spacing: 5px;
        position: absolute;
        left: 0;
        right: 0;
        bottom: -8px;
        margin-left: 6px; }

.footer {
  background-color: #392d3b;
  text-align: center;
  padding: 100px 0; }
  .footer ul {
    margin-bottom: 37px; }
    .footer ul li {
      display: inline-block;
      margin: 0 6px; }
      .footer ul li a {
        display: block;
        z-index: 1; }
        .footer ul li a object {
          width: 34px;
          height: auto;
          z-index: -1;
          pointer-events: none; }
  .footer .copyright {
    color: #fff;
    font-size: 12px;
    font-family: "Roboto", sans-serif;
    padding-bottom: 100px; }

.nav-icon {
  display: block;
  position: absolute;
  right: 20px;
  top: 50px;
  width: 36px;
  height: 36px;
  cursor: pointer; }
  .nav-icon .line {
    position: absolute;
    top: 7px;
    left: 50%;
    width: 30px;
    margin-left: -15px;
    height: 3px;
    background: #fff;
    transition: all 0.2s ease; }

.open .nav-item, .open .searsh-block {
  display: none; }

.open .nav-icon .line1 {
  top: 17px; }

.open .nav-icon .line3 {
  top: 27px; }

.close .searsh-block {
  display: block;
  position: relative;
  max-width: 275px;
  margin: 0 auto; }
  .close .searsh-block > * {
    display: inline-block;
    margin-right: 15px; }
  .close .searsh-block form input {
    background-color: unset;
    border: none;
    border-bottom: 1px solid #fff;
    padding: 3px; }
    .close .searsh-block form input:focus {
      outline: none; }
  .close .searsh-block form .empty {
    background-image: url("../img/icons/magnifying-glass.svg");
    background-repeat: no-repeat;
    background-position: 158px; }
  .close .searsh-block a {
    z-index: 1;
    position: relative; }
    .close .searsh-block a::before {
      content: '';
      border-left: 1px solid #fff;
      position: absolute;
      display: block;
      height: 20px;
      left: -5px; }
    .close .searsh-block a object {
      height: 20px;
      width: auto;
      z-index: -1;
      pointer-events: none; }
  .close .searsh-block .basket-counter {
    position: absolute;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #392d3b;
    padding-top: 3px;
    font-weight: 800;
    right: -18px;
    bottom: 6px; }

.close .nav-item {
  float: right;
  margin-top: 38px;
  padding-right: 130px;
  transition: all 1s ease;
  display: block;
  opacity: 1;
  list-style: none; }
  .close .nav-item li {
    display: inline; }
  .close .nav-item a {
    margin: 0 2px;
    color: #000;
    text-decoration: none;
    text-transform: uppercase; }

.close .nav-icon .line {
  top: 17px; }

.close .nav-icon .line1 {
  transform: rotateZ(45deg); }

.close .nav-icon .line2 {
  opacity: 0; }

.close .nav-icon .line3 {
  transform: rotateZ(-45deg); }

.galery-first .product-tshort, .galery-first .product-clearance {
  position: relative;
  /* &::before {
            @include overlay(#ffffff83);
        } */ }
  .galery-first .product-tshort .img-container, .galery-first .product-clearance .img-container {
    width: 100%;
    height: 293px;
    position: relative;
    overflow: hidden; }
    .galery-first .product-tshort .img-container img, .galery-first .product-clearance .img-container img {
      min-width: 100%;
      min-height: 100%;
      height: auto;
      position: relative;
      top: 50%;
      left: 50%;
      transform: translateY(-50%) translateX(-50%);
      align-self: center;
      transition: all 1s ease-out; }
  .galery-first .product-tshort .description, .galery-first .product-clearance .description {
    text-align: center;
    background-color: #ffffff9e;
    padding: 3px 10px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -106%); }
    .galery-first .product-tshort .description a, .galery-first .product-clearance .description a {
      color: #392d3b;
      font-size: 32px;
      transition: all 1s ease-out; }
      .galery-first .product-tshort .description a:hover, .galery-first .product-clearance .description a:hover {
        color: #dc5352; }
    .galery-first .product-tshort .description span, .galery-first .product-clearance .description span {
      display: block;
      color: #5a495f;
      font-size: 16px; }

@media screen and (min-width: 768px) {
  .galery-first .product-tshort, .galery-first .product-clearance {
    display: inline-block;
    width: 50%;
    float: left; } }

@media screen and (min-width: 992px) {
  .galery-first .product-tshort .img-container, .galery-first .product-tshort .description, .galery-first .product-clearance .img-container, .galery-first .product-clearance .description {
    display: inline-block;
    float: left;
    width: 50%; }
  .galery-first .product-tshort .description, .galery-first .product-clearance .description {
    background-color: unset;
    transform: translate(0%, -55%); } }

*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -ms-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: #5a5e60; }

html, body {
  height: 100%; }

body {
  text-transform: uppercase;
  font-family: "Roboto", sans-serif; }
  body > * {
    position: relative;
    width: 100%;
    overflow: hidden; }
  body a {
    text-decoration: none; }
